import ReactDOM from 'react-dom';
import './scss/index.scss';
// import 'lib-flexible';
import {
  Router as HashRouter,
} from "react-router-dom";
import { routesConfig } from './config/RouteConfig';
import Routes from './components/Routes';
import './utils/rem';
import createHistory from 'history/createHashHistory'
import GlobalConfig from './config/GlobalConfig';
import { mainStore } from './stores/MainStore';
import { LocalStorageService } from './utils/storage';
import { userStore } from './stores/UserStore';
const history = createHistory();
var _lang = LocalStorageService.get('_lang');
if (_lang) {
  mainStore.isEn = _lang == 'EN';
} else {
  var navigator: any = window.navigator;
  var language = (navigator.language || navigator.browserLanguage).toLowerCase();
  var isCh = language.toLowerCase().indexOf('zh') != -1;
  mainStore.isEn = !isCh;
}
let isLogin = LocalStorageService.get("_userInfo");
if (isLogin) {
  userStore.refreshUser();
}

ReactDOM.render(
  <HashRouter history={history}>
    <Routes routes={routesConfig} />
  </HashRouter>,
  document.getElementById('root')
);

